    <div class="content">
        <ul>
            <li>250 g de lait</li>
            <li>1/2 gousse de vanille</li>
            <li>2 jaunes d’œufs</li>
            <li>25g de sucre</li>
            <li>90g de poudre à crème</li>
            <li>25g de beurre</li>
            <li>25g de praliné noix de pécan</li>
            <li>20g de noix de pécan caramélisées</li>
            <li>1 pâte feuilletée de 35 x 25 cm</li>
            <li>100g de crème liquide</li>
            <li>sucre glace</li>
        </ul>

    </div>

    <div class="content">
        <h2 class="title">Étapes :</h2>
        <h3 class="title"><u>Préparation de la crème</u></h3>
        <p>
            Faire bouillir dans une casserole le lait et la vanille fendue en deux et grattée. Retirer la gousse. Mélanger à part les jaunes d’œufs, le sucre et la poudre à crème. Verser un peu de lait chaud dans cet appareil, mélanger, puis verser l’ensemble dans la casserole et porter à ébullition 1 minute. Quand la crème est cuite, ajouter le beurre, mélanger, couvrir d’un film et refroidir très rapidement au congélateur. Ajouter à la spatule la crème liquide montée nature. Verser la préparation dans une poche munie d’une douille unie et réserver au frais.
        </p>
        <h3 class="title"><u>Cuisson de la pâte feuilletée</u></h3>
        <p>
            Enfourner la pâte feuilletée à 160 °C, puis la cuire à 210 °C pendant 20 minutes. Saupoudrer de sucre glace et enfourner de nouveau quelques secondes. Tailler en rectangles de 5 cm sur 11 cm, soit en 12 rectangles. Laisser refroidir.
        </p>
        <h3 class="title"><u>Dressage</u></h3>
        <p>
            Dans les assiettes, déposer une première pâte feuilletée puis 3 couches : de crème, de praliné puis d‘éclats de noix de pécan caramélisées. Recouvrir d’une pâte feuilletée et renouveler les 3 couches crème-praliné-éclats. Terminer par une feuille de pâte feuilletée. Servir.
        </p>
    </div>

